'use client';

import { useEffect, useState } from 'react';

export default function Countdown() {
  const [timeLeft, setTimeLeft] = useState(null);

  useEffect(() => {
    const targetTime = new Date();
    targetTime.setHours(10, 0, 0, 0); // Nedeljom u 10h

    const updateCountdown = () => {
      const now = new Date();
      let diff = targetTime - now;

      // Ako je prošlo 10h, dodaj 7 dana
      if (diff < 0) {
        targetTime.setDate(targetTime.getDate() + 7);
        diff = targetTime - now;
      }

      const hours = Math.floor(diff / 1000 / 60 / 60);
      const minutes = Math.floor((diff / 1000 / 60) % 60);
      const seconds = Math.floor((diff / 1000) % 60);

      setTimeLeft({ hours, minutes, seconds });
    };

    updateCountdown(); // inicijalni poziv
    const interval = setInterval(updateCountdown, 1000);
    return () => clearInterval(interval);
  }, []);

  if (!timeLeft) return null;

  return (
    <section className="bg-[#1c1f3f] text-white text-center py-4">
      <h2 className="text-xl font-bold">До следеће службе:</h2>
      <div className="text-3xl font-mono mt-2">
        {timeLeft.hours}ч : {timeLeft.minutes}м : {timeLeft.seconds}с
      </div>
    </section>
  );
}
